import React, { useState } from 'react';
import './App.css';
import ResultView from './components/ResultView';
import Keyboard from './components/Keyboard';

let output = '';
let history = '';
let reset = 0;

function App() {
	const [state, setState] = useState({
		history: '',
		displayValue: ''
	});

	const onClick = (id, type, value) => {
		output = output.toString();
		let lastInput = output.slice(-1);
		let symbols = ['*', '-', '+', '/'];

		if (reset) {
			history = output;
			output = '';
			reset = 0;
		}

		if (output === '' && type === 'oper') {
			return;
		} else {
			if (type === 'func') {
				if (id === 'clear') {
					output = '';
					history = '0';
				} else if (id === 'clearBack') {
					output = output.slice(0, -1);
				} else if (id === 'calc' && !symbols.includes(lastInput)) {
					try {
						history = output;
						output = eval(output.replace(/%/g, '*0.01'));
						output = Number.isInteger(output) ? output : output.toFixed(3);
						reset = 1;
					} catch (error) {
						history = 'Error';
						output = '';
					}
				}
			} else if (type === 'oper') {
				symbols.includes(lastInput)
					? (output = output.slice(0, -1) + value)
					: (output += value);
			} else {
				if (id === 'dot' || id === '%') {
					if (output.includes('.') && id === 'dot') {
					} else {
						if (lastInput !== '.' && output !== '' && lastInput !== '%') {
							output += value;
						}
					}
				} else {
					output += value;
				}
			}
		}
		setState({ history: history, displayValue: output.toString() });
	};

	return (
		<div className="app">
			<div className="container">
				<ResultView history={state.history} output={state.displayValue} />

				<Keyboard onClick={onClick} />
			</div>
		</div>
	);
}

export default App;
